@import '../../styles/mixins';

.sb-select__menu-portal {
  z-index: calc(var(--sbt-stack-modal) + 100) !important;
}

.sb-select,
.sb-select__menu-portal {
  .sb-select__control {
    background: var(--sbt-white);
    border: var(--sbt-input-border-size) solid var(--sbt-input-border-color);
    border-radius: var(--sbt-input-border-radius);
    height: var(--sbt-input-height);
    min-height: unset;
    transition: all 80ms cubic-bezier(0.2, 0, 0.4, 0.9);

    &:hover {
      border-color: var(--sbt-input-border-color);
    }
  }

  .sb-select__value-container {
    padding: 0 0.5rem;
  }

  .sb-select__single-value,
  .sb-select__placeholder {
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-100);
    margin: 0;
    max-width: calc(100% - 1rem);
  }

  .sb-select__single-value {
    color: var(--sbt-theme-tertiary-color-90);
  }

  .sb-select__placeholder {
    color: var(--sb-select__placeholder, var(--sbt-theme-tertiary-color-30));
  }

  .sb-select__indicator-separator {
    background-color: var(
      --sb-select-separator-color,
      var(--sbt-theme-tertiary-color-30)
    );
  }

  .sb-select__indicator {
    color: var(--sb-select-indicator-color, var(--sbt-theme-tertiary-color-30));
    padding: 0.5rem;

    &:hover {
      color: var(--sbt-theme-tertiary-color-50);
    }
  }

  .sb-select__control--is-focused.sb-select__control--is-focused {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
    outline: none;

    .sb-select__indicator {
      color: var(--sbt-theme-tertiary-color-50);

      &:hover {
        color: var(--sbt-theme-tertiary-color-50);
      }
    }
  }

  .sb-select--is-disabled {
    // NOTE: overriding to allow disabled-shake which depends on hover event
    pointer-events: initial;
  }

  .sb-select__control--is-disabled {
    @include disabled-shake();

    background-color: var(--sbt-color-disabled-background);
    border-color: var(--sbt-color-disabled-border);
    cursor: not-allowed;

    .sb-select__single-value,
    .sb-select__placeholder {
      color: var(--sbt-color-disabled-text);
    }

    .sb-select__indicator {
      &:hover {
        color: var(--sbt-theme-tertiary-color-30);
      }
    }
  }

  .sb-select__menu {
    background-color: var(
      --sb-select-menu-background,
      var(--sbt-color-background)
    );
    border-radius: 0 0 var(--sbt-border-radius-small)
      var(--sbt-border-radius-small);
    box-shadow: var(--sb-select-menu-elevation, var(--sbt-elevation-30));
    margin: 8px 0;
    width: var(--sb-select-menu-width, 100%);
  }

  .sb-select__menu-list {
    padding: 0;
  }

  .sb-select__option {
    background-color: transparent;
    border: var(--sb-select__option-border, none);
    color: var(--sbt-theme-tertiary-color-80);
    cursor: pointer;
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-50);
    padding: 0.5rem;
    transition: 0.2s ease;
  }

  .sb-select__option--is-focused:not(.sb-select__option--is-disabled) {
    background-color: var(--sbt-theme-color-20);
  }

  .sb-select__option--is-selected.sb-select__option--is-selected {
    background-color: var(--sbt-theme-color-50);

    &:hover {
      background-color: var(--sbt-theme-color-30);
    }
  }

  .sb-select__option--is-disabled {
    --sb-typography-color: var(--sbt-color-disabled-text);
    color: var(--sbt-color-disabled-text);
    cursor: not-allowed;

    &:hover {
      background-color: transparent;
    }
  }

  .sb-select__group {
    padding: 0.375rem 0;
  }

  .sb-select__group-heading {
    color: var(--sbt-theme-tertiary-color-50);
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-50);
    margin: 0;
    padding: 0.5rem;
    text-transform: none;
  }

  .sb-select__menu-notice {
    color: var(--sbt-theme-tertiary-color-50);
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-50);
    padding: 0.5rem;
    text-align: initial;
  }

  .sb-select__loading-indicator {
    padding: 0.375rem;
  }

  // Select with custom icon
  // --sb-select-icon-color
  .sb-select__icon-container {
    color: var(--sb-select__icon-color, var(--sbt-theme-tertiary-color-50));
    margin-right: 0.375rem;
  }
}

.sb-select {
  &.sb-select__flush-right .sb-select__control {
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
  }

  &.sb-select__flush-left .sb-select__control {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }

  &.sb-select__flush .sb-select__control {
    border-radius: 0;
  }
}

// --sb-select-button-background-color
// --sb-select-button-color
// --sb-select-button-hover-background-color

.sb-select-button__button {
  --sb-button-color: var(
    --sb-select-button-background-color,
    var(--sbt-theme-tertiary-color-10)
  );
  --sb-button-color-contrast: var(
    --sb-select-button-color,
    var(--sbt-theme-color-70)
  );
  --sb-button-color-dark: var(
    --sb-select-button-hover-background-color,
    var(--sbt-theme-tertiary-color-20)
  );
  justify-content: center;
  width: 100%;
}

.sb-select_error {
  @include error();
  border: none;
  padding: var(--sbt-spacing-0);

  .sb-select__control,
  .sb-select__control--is-focused.sb-select__control--is-focused {
    border: var(--sbt-stroke-thin-error);
    box-shadow: var(--sbt-elevation-0);

    &:hover {
      border: var(--sbt-stroke-thin-error);
      box-shadow: var(--sbt-elevation-error);
    }
  }
}
