@import '../../styles/mixins';
@import '../../styles/constants';
@import '../../styles/animations';

// SB Button Variables:
// --sb-spinner-color (color of spinner for use with Mask backgrounds)
// --sb-spinner-size (size of spinner)
// --sb-spinner-bg-size (mask-size/background-size)
// --sb-spinner-animation-time (length of animation effects, ie. single spin rotation, single bounce, etc)

.sb-spinner {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sb-spinner_mask {
  background-color: var(--sb-spinner-color, var(--sbt-theme-tertiary-color-80));
  -webkit-mask: url($img-url + '/Spinner-prism-48px.svg');
  mask: url($img-url + '/Spinner-prism-48px.svg');
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-size: var(--sb-spinner-bg-size, cover);
  mask-size: var(--sb-spinner-bg-size, cover);
}

.sb-spinner_background {
  background-image: url($img-url + '/Spinner-prism-48px.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: var(--sb-spinner-bg-size, cover);
}

.sb-spinner_animate_spin {
  animation: sb-spin var(--sb-spinner-animation-time, 1s) linear infinite;
}

.sb-spinner_animate_bounce {
  animation: sb-bounce var(--sb-spinner-animation-time, 1s) linear infinite;
}

.sb-spinner_animate_wobble {
  animation: sb-wobble var(--sb-spinner-animation-time, 1s) linear infinite;
}

.sb-spinner_animate_rock {
  animation: sb-rock var(--sb-spinner-animation-time, 1s) linear infinite;
}

.sb-spinner_smaller {
  height: var(--sb-spinner-size, 16px);
  width: var(--sb-spinner-size, 16px);
}

.sb-spinner_small {
  height: var(--sb-spinner-size, 24px);
  width: var(--sb-spinner-size, 24px);
}

.sb-spinner_medium {
  height: var(--sb-spinner-size, 40px);
  width: var(--sb-spinner-size, 40px);
}

.sb-spinner_large {
  height: var(--sb-spinner-size, 80px);
  width: var(--sb-spinner-size, 80px);
}

.sb-spinner-load-text {
  @include sr-only();
}
