@import '../../styles/mixins';

// SB Button Variables:
// --sb-button-border-radius
// --sb-button-color - background and outline
// --sb-button-color-contrast - text color
// --sb-button-color-dark - hover text color
// --sb-button-color-light - hover background
// --sb-button-icon-color - icon color
// --sb-button-fixed-height - Fixed height
// --sb-button-font-size-(large/small/smaller) - font size
// --sb-button-font-weight - font weight
// --sb-button-padding-vertical-(large/small/smaller) - vertical padding
// --sb-button-padding-horizontal-(large/small/smaller) - horizontal padding

.sb-button {
  border-radius: var(--sb-button-border-radius, var(--sbt-border-radius-small));
  border-style: solid;
  border-width: var(--sbt-button-border-size);
  cursor: pointer;
  display: inline-block;
  font-size: var(--sb-button-font-size, var(--sbt-font-size-30));
  font-weight: var(--sb-button-font-weight, var(--sbt-font-weight-40));
  height: var(--sb-button-fixed-height, auto);
  line-height: var(--sbt-line-height-ui);
  margin: 0;
  padding: var(--sb-button-padding-vertical, 0.75em)
    var(--sb-button-padding-horizontal, 1.25em);
  text-align: center;
  text-decoration: none;
  transition: all 0.2s ease;

  &:focus {
    box-shadow: var(--sbt-elevation-40);
    outline: 0;
  }

  &:disabled {
    box-shadow: none;
    cursor: not-allowed;

    &.sb-shake-on-disabled {
      @include disabled-shake();
    }
  }
}

.sb-button-text {
  padding: var(--sb-button-text-padding, initial);
}

.sb-button__flush {
  --sb-button-border-radius: 0;
}

.sb-button__flush-right {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.sb-button__flush-left {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.sb-button_icon {
  color: var(--sb-button-icon-color, inherit);
}

.sb-button_icon-before i {
  margin-right: 0.5em;
}

.sb-button_icon-after i {
  margin-left: 0.5em;
}

.sb-button_icon-only i {
  margin-right: 0;
}

// fetching
.sb-button_fetching {
  align-items: center;
  display: inline-flex;
  flex-direction: row;

  &.sb-button_smaller,
  &.sb-button_small {
    --sb-spinner-size: 16px;
  }

  &.sb-button_large {
    --sb-spinner-size: 32px;
  }
}

.sb-button_no-icon {
  position: relative;

  // In order to keep the width of the button, we need to hide the original text without removing it from the DOM (ie. display: none)
  &.sb-button_fetching .sb-button-text {
    opacity: 0;
  }

  // Center the spinner where the text used to be
  &.sb-button_fetching .sb-button_spinner-container {
    left: 0;
    position: absolute;
    right: 0;
    width: 100%;
  }

  // Center the spinner where the text used to be
  &.sb-button_fetching .sb-button_spinner {
    display: block;
    margin: 0 auto;
    width: fit-content;
  }
}

// Style type variants

.sb-button_promoted {
  --sb-spinner-color: var(
    --sb-button-color-contrast,
    var(--sbt-color-text-invert)
  );
  background-color: var(--sb-button-color);
  border-color: var(--sb-button-color);
  color: var(--sb-button-color-contrast, var(--sbt-color-text-invert));

  &:hover {
    --sb-button-color: var(--sb-button-color-dark);
  }

  // NOTE: Keep `&:disable` after `&:hover` because of specificity
  &:disabled {
    --sb-button-color: var(--sbt-color-disabled-background);
    --sb-button-color-contrast: var(--sbt-color-disabled-text);
  }
}

.sb-button_standard {
  --sb-spinner-color: var(--sb-button-color-contrast, var(--sb-button-color));
  background-color: transparent;
  border-color: var(--sb-button-color);
  color: var(--sb-button-color);
  font-weight: var(--sb-button-font-weight, var(--sbt-font-weight-60));

  &:hover {
    --sb-button-color: var(--sb-button-color-dark);
  }

  // NOTE: Keep `&:disable` after `&:hover` because of specificity
  &:disabled {
    --sb-button-color: var(--sbt-color-disabled-background);
    --sb-button-color-contrast: var(--sbt-color-disabled-text);
  }
}

.sb-button_demoted {
  --sb-spinner-color: var(--sb-button-color);
  background-color: transparent;
  border-color: transparent;
  color: var(--sb-button-color);
  font-weight: var(--sb-button-font-weight, var(--sbt-font-weight-60));

  &:hover {
    background-color: var(--sb-button-color-light);
    color: var(--sb-button-color);
  }

  &:disabled {
    --sb-spinner-color: var(--sbt-theme-tertiary-color-20);
    background-color: var(--sbt-theme-tertiary-color-10);
    border-color: transparent;
    color: var(--sbt-theme-tertiary-color-20);
  }
}

.sb-button_link {
  --sb-spinner-color: var(--sb-button-color);
  background-color: transparent;
  border-color: transparent;
  color: var(--sb-button-color);
  font-weight: var(--sb-button-font-weight, var(--sbt-font-weight-60));
  margin: 0.75em 1.25em;
  padding: 0;

  &:hover {
    --sb-button-color: var(--sb-button-color-dark);
  }

  &:focus {
    box-shadow: none;
  }

  &:disabled {
    --sb-spinner-color: var(--sbt-theme-tertiary-color-20);
    border-color: transparent;
    color: var(--sbt-theme-tertiary-color-20);
  }
}

// Size variants

.sb-button_large {
  --sb-button-icon-only-width: 54px;
  font-size: var(--sb-button-font-size-large, var(--sbt-font-size-35));
  padding: var(--sb-button-padding-vertical-large, 1rem)
    var(--sb-button-padding-horizontal-large, 1.25em);

  &.sb-button_link {
    margin: 1rem 1.25em;
    padding: 0;
  }

  i {
    font-size: var(--sbt-font-size-45);
    line-height: 0.75em;
    vertical-align: -0.0667em;
  }
}

.sb-button_small {
  --sb-button-icon-only-width: 34px;
  font-size: var(--sb-button-font-size-small, var(--sbt-font-size-25));
  padding: var(--sb-button-padding-vertical-small, 0.5rem)
    var(--sb-button-padding-horizontal-small, 1.25em);

  &.sb-button_link {
    margin: 0.5rem 1.25em;
    padding: 0;
  }

  i {
    font-size: var(--sbt-font-size-25);
  }
}

.sb-button_smaller {
  --sb-button-icon-only-width: 24px;
  font-size: var(--sb-button-font-size-smaller, var(--sbt-font-size-20));
  padding: var(--sb-button-padding-vertical-smaller, 0.2rem)
    var(--sb-button-padding-horizontal-smaller, 1.25em);

  &.sb-button_link {
    margin: 0.2rem 1.25em;
    padding: 0;
  }

  i {
    font-size: var(--sbt-font-size-20);
  }
}

.sb-button_auto {
  --sb-button-icon-only-width: 100%;
  font-size: var(--sb-button-font-size, var(--sbt-font-size-25));
  height: 100%;
  padding: var(--sb-button-padding-vertical, 0.5rem)
    var(--sb-button-padding-horizontal, 1.25em);
  width: 100%;

  &.sb-button_link {
    margin: 0.5rem 1.25em;
    padding: 0;
  }

  i {
    font-size: var(--sbt-font-size-25);
  }
}

// Color type variants

.sb-button_primary {
  --sb-button-color: var(--sb-button-primary-color, var(--sbt-theme-color-70));
  --sb-button-color-dark: var(
    --sb-button-primary-color-dark,
    var(--sbt-theme-color-80)
  );
  --sb-button-color-light: var(
    --sb-button-primary-color-light,
    var(--sbt-theme-color-10)
  );
}

.sb-button_secondary {
  --sb-button-color: var(
    --sb-button-secondary-color,
    var(--sbt-theme-color-60)
  );
  --sb-button-color-dark: var(
    --sb-button-secondary-color-dark,
    var(--sbt-theme-color-80)
  );
  --sb-button-color-light: var(
    --sb-button-secondary-color-light,
    var(--sbt-theme-color-10)
  );
}

.sb-button_success {
  --sb-button-color: var(
    --sb-button-success-color,
    var(--sbt-color-success-50)
  );
  --sb-button-color-dark: var(
    --sb-button-success-color-dark,
    var(--sbt-green-80)
  );
  --sb-button-color-light: var(
    --sb-button-success-color-light,
    var(--sbt-green-10)
  );
}

.sb-button_error {
  --sb-button-color: var(--sb-button-error-color, var(--sbt-color-error-50));
  --sb-button-color-dark: var(--sb-button-error-color-dark, var(--sbt-red-80));
  --sb-button-color-light: var(
    --sb-button-error-color-light,
    var(--sbt-red-30)
  );
}

.sb-button_warning {
  --sb-button-color: var(
    --sb-button-warning-color,
    var(--sbt-color-warning-50)
  );
  --sb-button-color-dark: var(
    --sb-button-warning-color-dark,
    var(--sbt-yellow-80)
  );
  --sb-button-color-light: var(
    --sb-button-warning-color-light,
    var(--sbt-orange-10)
  );
}

// Stacked

.sb-button_icon-only,
.sb-button_icon-stacked {
  height: var(
    --sb-button-icon-only-height,
    var(--sb-button-icon-only-width, 44px)
  );
  padding: 0;
  width: var(--sb-button-icon-only-width, 44px);

  &.sb-button_link {
    margin-left: 0;
    margin-right: 0;
    padding: 0;
  }
}

.sb-stacked-wrapper {
  background-color: inherit;
  border: none;
  cursor: pointer;
  display: inline-grid;
  justify-items: center;
}

.sb-stacked-wrapper-disabled {
  cursor: not-allowed;
}

.sb-stacked-label {
  color: var(--sb-button-color);
  font-size: var(--sb-button-font-size, var(--sbt-font-size-25));
  margin-top: 5px;
  text-align: center;
}

// If under a stacked wrapper, need to call out individual styles
.sb-stacked-wrapper-disabled {
  .sb-button_disabled {
    box-shadow: none;

    &.sb-shake-on-disabled {
      @include disabled-shake();
    }
  }

  .sb-button_promoted,
  .sb-button_promoted:hover,
  .sb-button_link,
  .sb-button_link:hover {
    --sb-button-color: var(--sbt-theme-tertiary-color-20);
    --sb-button-color-contrast: var(--sbt-theme-tertiary-color-30);
    cursor: not-allowed;
  }

  .sb-button_standard,
  .sb-button_standard:hover {
    --sb-button-color: var(--sbt-theme-tertiary-color-20);
    cursor: not-allowed;
  }

  .sb-button_demoted,
  .sb-button_demoted:hover {
    background-color: var(--sbt-theme-tertiary-color-10);
    border-color: transparent;
    color: var(--sbt-theme-tertiary-color-20);
    cursor: not-allowed;
  }
}

// fetching variants
.sb-button_icon-before,
.sb-button_icon-after,
.sb-button_icon-only,
.sb-button_icon-stacked {
  &.sb-button_fetching {
    justify-content: center;
    position: relative;

    .sb-button_icon-container {
      position: relative;
    }

    .sb-button_spinner-container {
      display: inline-flex;
      justify-content: center;
      position: absolute;
    }

    .sb-button_icon {
      visibility: hidden;
    }
  }
}

.sb-button_icon-before,
.sb-button_icon-after {
  &.sb-button_fetching {
    --sb-spinner-size: var(--sbt-font-size-30);

    &.sb-button_smaller {
      --sb-spinner-size: var(--sbt-font-size-20);
    }

    &.sb-button_small {
      --sb-spinner-size: var(--sbt-font-size-25);
    }

    &.sb-button_large {
      --sb-spinner-size: 1.33333em;
    }

    .sb-button_spinner-container {
      left: 0;
      width: 100%;
    }
  }
}

.sb-button_icon-before,
.sb-button_icon-after {
  &.sb-button_fetching.sb-button_large {
    .sb-button_spinner-container {
      top: -2.75px;
    }
  }
}

.sb-button_icon-before {
  &.sb-button_fetching .sb-button_spinner-container {
    justify-content: flex-start;
  }
}

.sb-button_icon-after {
  &.sb-button_fetching .sb-button_spinner-container {
    justify-content: flex-end;
  }
}

.sb-button_icon-stacked {
  &.sb-button_fetching.sb-button_large {
    padding-bottom: 1.025rem;
    padding-top: 1.025rem;
  }
}

.sb-button_icon-only {
  &.sb-button_fetching {
    --sb-spinner-size: 24px;

    .sb-button_spinner-container {
      left: -4.75px;
      top: -4.25px;
    }

    &.sb-button_smaller,
    &.sb-button_small {
      --sb-spinner-size: 16px;
    }

    &.sb-button_smaller .sb-button_spinner-container {
      left: -2.75px;
      top: -1.75px;
    }

    &.sb-button_small .sb-button_spinner-container {
      left: -2.25px;
      top: -1.5px;
    }

    &.sb-button_large {
      --sb-spinner-size: 32px;

      .sb-button_spinner-container {
        left: -6px;
        top: -7.25px;
      }
    }
  }
}
